.heading {
  width: max-content;
  position: relative;
  margin: 60px auto;
}
.heading h1::before {
  content: "";
  position: absolute;
  left: -23px;
  width: 5px;
  top: 0;
  height: 100%;
  background: brown;
  border-radius: 5px;
  transform: rotate(18deg);
}
.heading h1::after {
  content: "";
  position: absolute;
  right: -23px;
  width: 5px;
  top: 0;
  height: 100%;
  background: brown;
  border-radius: 5px;
  transform: rotate(18deg);
}
.review-section {
  width: 800px;
  margin: auto;
  height: 400px;
  text-align: center;
  position: relative;
  overflow: hidden;
}
.main-wrapper{
  position: relative;
}
.review {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  transition: .3s;
  opacity: 0;
}

.next-slide {
  transform: translateX(100%);
}
.active-slide {
  opacity: 1;
  transform: translateX(0);
}
.prev-slide {
  transform: translateX(-100%);
}

.img-wrapper img {
  height: 120px;
  width: 120px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 5px 10px 15px -2px #79554859;
}
h4.name {
  text-transform: uppercase;
  color: firebrick;
  font-size: 18px;
  margin-bottom: 8px;
}
span.title {
  text-transform: capitalize;
  color: #4f656f;
}
.review-text {
  margin-top: 40px;
  position: relative;
}
.review-text p {
  line-height: 2;
  color: #4f656f;
  font-style: italic;
}
.quote-left {
  position: absolute;
  top: -35px;
  color: firebrick;
  font-size: 30px;
}
.quote-right {
  position: absolute;
  bottom: -15px;
  right: 0;
  color: firebrick;
  font-size: 30px;
}
.controls span {
  background: #b222222e;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  font-size: 24px;
  justify-content: center;
  color: #670000;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  transition: 0.3s;
}
.controls span:hover {
  background-color: #b2222254;
}
.controls span.left {
  left: 200px;
}
.controls span.right {
  right: 200px;
}
